import React, { Component } from 'react';
import Home from "./components/Home/Home";
import './App.css';
import {Link, Route, Router} from "react-router-dom";
import Skills from "./components/Skills/Skills";
import createBrowserHistory from "history/createBrowserHistory";

const history = createBrowserHistory();
const routes = {};

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isHome: true
    }

    // this.checkThisPage = checkThisPage.bind(this);
  }

  checkThisPage = () =>{
    this.setState({isHome: !this.state.isHome});
  };


  render() {
    let {isHome} = this.state;
    return (
      <div className="App">
        <Router history={history}>
          <div className="r">
            <ul className="menu_list">
              <li className="menu_item">
                {isHome && <Link className="home_link_arrow" onClick={this.checkThisPage} to="/skills">Подробнее о скилах</Link>}
                {!isHome && <Link className="home_link_arrow" onClick={this.checkThisPage} to="/">Домой</Link>}
              </li>

              <li className="menu_item">
                <Link className="home_link_arrow" to="/about">Обо мне</Link>
              </li>

              <li className="menu_item">
                <Link className="home_link_arrow" to="/contacts">Контакты</Link>
              </li>
            </ul>

            <Route exact path="/" component={Home}/>
            <Route path="/skills" component={Skills}/>
          </div>
        </Router>
      </div>
    );
  }
}

export default App;
